<template>
  <div class="profile-page" v-if="userProfile">
    <div class="user-info">
      <div class="container">
        <div class="row">
          <div class="col-xs-12 col-md-10 offset-md-1">
            <img class="user-img" :src="userProfile.image" />
            <h4>{{ userProfile.username }}</h4>
            <p>{{ userProfile.bio }}</p>
            <button
              v-if="!isCurrentUserProfile"
              class="btn btn-sm action-btn btn-outline-secondary"
            >
              <i class="ion-plus-round"></i>
              &nbsp; Follow {{ userProfile.username }}
            </button>
            <router-link
              v-if="isCurrentUserProfile"
              class="btn btn-sm btn-outline-secondary action-btn"
              :to="{name: 'settings'}"
            >
              <i class="ion-gear-a"></i> Edit Profile Settings
            </router-link>
          </div>
        </div>
      </div>
    </div>
    <div class="container">
      <div class="row">
        <div class="col-xs-12 col-md-10 offset-md-1">
          <div class="articles-toggle">
            <ul class="nav nav-pills outline-active">
              <li class="nav-item">
                <router-link
                  class="nav-link"
                  :class="{active: routeName === 'userProfile'}"
                  :to="{
                    name: 'userProfile',
                    params: {slug: userProfile.username},
                  }"
                >
                  My Articles
                </router-link>
              </li>

              <li class="nav-item">
                <router-link
                  class="nav-link"
                  :class="{active: routeName === 'userProfileFavorites'}"
                  :to="{
                    name: 'userProfileFavorites',
                    params: {slug: userProfile.username},
                  }"
                >
                  Favorited Articles
                </router-link>
              </li>
            </ul>
          </div>
          <mcv-feed :apiUrl="apiUrl" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapState, mapGetters} from 'vuex'

import {actionTypes as userProfileActionTypes} from '@/store/modules/userProfile'
import {getterTypes as authGetterTypes} from '@/store/modules/auth'
import McvFeed from '@/components/Feed'

export default {
  name: 'McvUserProfile',
  components: {
    McvFeed,
  },
  computed: {
    ...mapState({
      isLoading: state => state.userProfile.isLoading,
      userProfile: state => state.userProfile.data,
      error: state => state.userProfile.error,
    }),
    ...mapGetters({
      currentUser: authGetterTypes.currentUser,
    }),
    isCurrentUserProfile() {
      if (!this.currentUser || !this.userProfile) {
        return false
      }

      return this.currentUser.username === this.userProfile.username
    },
    userProfileSlug() {
      return this.$route.params.slug
    },
    apiUrl() {
      const isFavorites = this.$route.path.includes('favorites')
      return isFavorites
        ? `/articles?favorited=${this.userProfileSlug}`
        : `/articles?author=${this.userProfileSlug}`
    },
    routeName() {
      return this.$route.name
    },
  },
  watch: {
    userProfileSlug() {
      this.getUserProfile()
      this.apiUrl()
    },
  },
  mounted() {
    this.getUserProfile()
  },
  methods: {
    getUserProfile() {
      this.$store.dispatch(userProfileActionTypes.getUserProfile, {
        slug: this.userProfileSlug,
      })
    },
  },
}
</script>

